  top: 0;
  transition: top 0.2s ease-in-out;
  width: 100%; }

.headroom--pinned {
  top: 0; }

.headroom--unpinned {
  top: -40px; }

/* ---- grid ---- */
.masonry {
  z-index: 0;
  top: 60px; }

/* clear fix */
.masonry:after {
  content: '';
  display: block;
  clear: both; }

/* ---- .masonry-item ---- */
.masonry-sizer,
.masonry-item {
  width: 50%; }

.masonry-item {
  float: left; }

