  display: block;
  max-width: 100%; }

.burger-box {
  z-index: 1001;
  position: absolute;
  top: 7px;
  left: 1rem;
  display: block;
  width: 24px;
  height: 24px;
  cursor: pointer; }

.burger {
  content: '';
  font-size: 0px;
  user-select: none;
  position: absolute;
  top: 50%;
  left: 0;
  width: 24px;
  height: 2px;
  background: #333;
  transition: background 0.3s, transform 0.3s, top 0.3s; }
  .burger:before {
    position: absolute;
    top: 50%;
    left: 0;
    width: 24px;
    height: 2px;
    background: #333;
    transition: background 0.3s, transform 0.3s, top 0.3s;
    top: -8px;
    left: 0;
    content: ""; }
  .burger:after {
    position: absolute;
    top: 50%;
    left: 0;
    width: 24px;
    height: 2px;
    background: #333;
    transition: background 0.3s, transform 0.3s, top 0.3s;
    top: 8px;
    left: 0;
    content: ""; }

.burger--open .burger {
  -webkit-transform: translateX(220px);
  -moz-transform: translateX(220px);
  -ms-transform: translateX(220px);
  -o-transform: translateX(220px);
  transform: translateX(220px);
  -webkit-transition: all 0.25s linear;
  -moz-transition: all 0.25s linear;
  transition: all 0.25s linear;
  background: transparent; }
  .burger--open .burger:before {
    top: 0;
    transform: rotateZ(-45deg); }
  .burger--open .burger:after {
    top: 0;
    transform: rotateZ(45deg); }

.sliding-panel {
  position: fixed;
  top: 0px;
  right: auto;
  bottom: 0px;
  left: 0px;
  height: 100%;
  width: 220px;
  -webkit-transform: translateX(-220px);
  -moz-transform: translateX(-220px);
  -ms-transform: translateX(-220px);
  -o-transform: translateX(-220px);
  transform: translateX(-220px);
  -webkit-transition: all 0.25s linear;
  -moz-transition: all 0.25s linear;
  transition: all 0.25s linear;
  background: #404040;
  z-index: 998;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch; }
  .sliding-panel ul {
    padding: 0;
    margin: 0; }
  .sliding-panel li {
    list-style: none; }
    .sliding-panel li:first-child {
      margin-top: 50px; }
  .sliding-panel li a {
    border-bottom: 1px solid #333;
    color: #fff;
    display: block;
    font-weight: bold;
    padding: 1em;
    text-decoration: none; }
    .sliding-panel li a:focus {
      background-color: #4d4d4d; }
    .sliding-panel li a:hover {
      background-color: #ddd;
      color: #fff; }
  .sliding-panel.sliding-panel--open {
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0); }

.screen-overlay {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  -webkit-transition: all 0.15s ease-out 0s;
  -moz-transition: all 0.15s ease-out 0s;
  transition: all 0.15s ease-out 0s;
  background: black;
  opacity: 0;
  visibility: hidden;
  z-index: 997; }
  .screen-overlay.screen-overlay--visible {
