.checkbox {
  display: none; }

.icon {
  position: absolute;
  right: 0;
  width: 1.625rem;
  height: .313rem;
  background: #000;
  margin: 1.5rem;
  transition: right 0.3s ease; }
  .icon::before,
  .icon::after {
    content: "";
    position: absolute;
    background: #000;
    width: 1.625rem;
    height: .313rem; }
  .icon::before {
    top: -.625rem; }
  .icon::after {
    bottom: -.625rem; }

.body-close {
  position: absolute;
  top: 0;
  right: 17.5rem;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1; }

.off-canvas {
  position: fixed;
  right: -17.5rem;
  width: 17.5rem;
  height: 100%;
  background: #ccc;
  transition: right 0.3s ease; }

.wrapper {
  position: relative;
  left: 0;
  transition: left 0.3s ease; }

.checkbox:checked ~ .off-canvas {
  right: 0; }

.checkbox:checked ~ .icon {
  right: 17.5rem; }

.checkbox:checked ~ .body-close {
  pointer-events: auto; }

.checkbox:checked ~ .wrapper {
  left: -17.5rem; }

